export default {
	data() {
		return {
			userName: '',
			phoneNum: '',
			idCardF: '../../static/idcardF.png',
			idCardB: '../../static/idcardB.png',
			payCardF: '../../static/payCardF.png',
			payCardB: '../../static/payCardB.png',
			idCardFX: '',
			idCardBX: '',
			payCardFX: '',
			payCardBX: '',
			machineNum:''
		}
	},
	methods: {
		submitInfo(){
			var that = this
			uni.request({
				url: this.baseUrl + 'index/uploadInfoImg',
				method:"POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data:{
					type:'3',
					zcard:that.idCardFX,
					fcard:that.idCardBX,
					zBankcard:that.payCardFX,
					fBankcard: that.payCardBX,
					mobile:that.phoneNum,
					name:that.userName,
					user_id:that.userInfo.user_id,
					machineNum: that.machineNum
				},
				success(res) {
					uni.showModal({
						content: res.data.msg,
						showCancel: false
					})
				}
			})
		},
		clearName() {
			this.userName = ''
		},
		clearPhone() {
			this.phoneNum = ''
		},
		clearNum() {
			this.machineNum = ''
		},
		upIDcardF() {
			var that = this
			uni.chooseImage({
				success(res) {
					that.idCardF = res.tempFilePaths[0]
					uni.uploadFile({
						url: that.baseUrl  + 'login/UpImg', //仅为示例，非真实的接口地址
						filePath: res.tempFilePaths[0],
						name: 'file',
						success: (uploadFileRes) => {
							var res = JSON.parse(uploadFileRes.data)
							if (res.code == 1) {
								that.idCardFX = res.path
								uni.showToast({
									title: '上传成功',
									duration: 2000
								});
							} else {
								uni.showToast({
									title: '网络错误,刷新试试',
									duration: 2000
								});
							}
						}
					});
				}
			})
		},
		upIDcardB() {
			var that = this
			uni.chooseImage({
				success(res) {
