								});
							} else {
								uni.showToast({
									title: '网络错误,刷新试试',
									duration: 2000
								});
							}
						}
					});
				}
			})
		},
		upPayCardF() {
			var that = this
			uni.chooseImage({
				success(res) {
					that.payCardF = res.tempFilePaths[0]
					uni.uploadFile({
						url: that.baseUrl  + 'login/UpImg', //仅为示例，非真实的接口地址
						filePath: res.tempFilePaths[0],
						name: 'file',
						success: (uploadFileRes) => {
							var res = JSON.parse(uploadFileRes.data)
							if (res.code == 1) {
								that.payCardFX = res.path
								uni.showToast({
									title: '上传成功',
									duration: 2000
								});
							} else {
								uni.showToast({
									title: '网络错误,刷新试试',
									duration: 2000
								});
							}
						}
					});
				}
			})
		},
		upPayCardB() {
			var that = this
			uni.chooseImage({
				success(res) {
					that.payCardB = res.tempFilePaths[0]
